import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { InicioPermisoPage } from './inicio-permiso.page';

const routes: Routes = [
  {
    path: '',
    component: InicioPermisoPage,
  },
  {
    path: 'seccion-general',
    loadChildren: () =>
      import('src/app/pages/SST/informacion-actividad/seccion-general/seccion-general.module').then(m => m.SeccionGeneralPageModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class InicioPermisoPageRoutingModule {}
