import { Injectable } from '@angular/core';
import { Geolocation } from 'src/app/entities/condiciones-salud';
import { Geolocation as GeolocationService, Geoposition } from '@ionic-native/geolocation/ngx';
import { UtilsService } from 'src/app/services/utils.service';

@Injectable({
  providedIn: 'root',
})
export class CurrentPostitionService {
  locationDevice: Geoposition = null;

  constructor(private utils: UtilsService, private geolocation: GeolocationService) {}

  getLocationDevice(): Geoposition {
    return this.locationDevice;
  }

  async getCurrentPosition(): Promise<void> {
    try {
      await this.utils.presentLoading();
      this.geolocation
        .getCurrentPosition()
        .then(resp => {
          this.onFinally(resp.coords.latitude.toString(), resp.coords.longitude.toString());
          this.locationDevice = resp;
        })
        .catch(error => {
          console.log('Error getting location', error);
        });
    } catch (error) {
      console.error('Error al obtener la ubicación:', error);
      this.onFinally();
    } finally {
      this.utils.dismissLoading();
    }
  }

  private onFinally(latitude?: string, longitude?: string): void {
    this.utils.dismissLoading();

    const geolocation = new Geolocation();
    geolocation.latitude = latitude;
    geolocation.longitude = longitude;
  }

  async verificarPermisos() {
    console.log('El navegador admite la API de permisos de geolocalización');
    if ('permissions' in navigator) {
      try {
        const result = await navigator.permissions.query({ name: 'geolocation' });

        if (result.state === 'granted') {
          this.getCurrentPosition();
        } else if (result.state === 'prompt') {
          console.log('Los permisos aún no se han otorgado');
        } else if (result.state === 'denied') {
          console.log('Los permisos están denegados');
          window.location.href = 'app-settings:';
        }
      } catch (error) {
        console.error('Error al verificar los permisos de geolocalización:', error);
      }
    }
  }
}
